import React, { Component } from "react";
import { Form, Button, FormGroup, Col } from "react-bootstrap";

import "../CssComponents/Home.css";

import RadioFields from "../assets/radioFields";

export default class Home extends Component {
  state = {
    days: [],
    months: [],
    years: [],
    age: 0,
    datosRadioArray: [],
    antecedentesMedicosRadioArray: [],
    analisisFacialRadioArray: [],
    analisisBucalRadioArray: [],
    examenRadiograficoRadioArray: [],
    analisisDeModelosRadioArray: [],
  };

  componentDidMount() {
    this.fillDayMonthYearArray();
    this.fillRadioArrays();
  }

  fillRadioArrays = () => {
    let datosRadioArray = RadioFields.filter(
      (field) => field.tabla === "datos"
    );
    let antecedentesMedicosRadioArrayEnter = RadioFields.filter(
      (field) => field.tabla === "antecedentesMedicos"
    );

    let chunk = 5;
    let antecedentesMedicosRadioArray = [];
    for (let i = 0; i < antecedentesMedicosRadioArrayEnter.length; i += chunk) {
      antecedentesMedicosRadioArray.push(
        antecedentesMedicosRadioArrayEnter.slice(i, i + chunk)
      );
    }

    let analisisFacialRadioArrayEnter = RadioFields.filter(
      (field) => field.tabla === "analisisFacial"
    );

    let analisisFacialRadioArray = [];
    for (let i = 0; i < analisisFacialRadioArrayEnter.length; i += chunk) {
      analisisFacialRadioArray.push(
        analisisFacialRadioArrayEnter.slice(i, i + chunk)
      );
    }

    let analisisBucalRadioArrayEnter = RadioFields.filter(
      (field) => field.tabla === "analisisBucal"
    );

    let analisisBucalRadioArray = [];
    for (let i = 0; i < analisisBucalRadioArrayEnter.length; i += chunk) {
      analisisBucalRadioArray.push(
        analisisBucalRadioArrayEnter.slice(i, i + chunk)
      );
    }

    let examenRadiograficoRadioArray = RadioFields.filter(
      (field) => field.tabla === "examenRadiografico"
    );
    let analisisDeModelosRadioArray = RadioFields.filter(
      (field) => field.tabla === "analisisDeModelos"
    );

    this.setState({
      datosRadioArray,
      antecedentesMedicosRadioArray,
      analisisFacialRadioArray,
      analisisBucalRadioArray,
      examenRadiograficoRadioArray,
      analisisDeModelosRadioArray,
    });
  };

  fillDayMonthYearArray = () => {
    let days = [];
    let months = [];
    let years = [];
    for (let i = 0; i < 31; i++) {
      days[i] = i + 1;
    }
    for (let i = 0; i < 12; i++) {
      months[i] = i + 1;
    }
    for (let i = 0; i < 102; i++) {
      years[i] = i + 1920;
    }
    this.setState({
      days,
      months,
      years,
    });
  };

  render() {
    return (
      <div id="home-div">
        <h1>Clinica de ortodoncia GNATOS</h1>
        <div id="formContainer-div">
          <Form>
            <Form.Row>
              <Form.Group as={Col} controlId="formBasicDoctor">
                <Form.Control
                  type="text"
                  placeholder="Nombre Doctor responsable"
                />
              </Form.Group>

              <Form.Group as={Col} controlId="formBasicGrupo">
                <Form.Control type="text" placeholder="Grupo" />
              </Form.Group>
            </Form.Row>

            <Form.Row>
              <Form.Group as={Col} controlId="formBasicNombreCompleto">
                <Form.Label>Datos de pasiente</Form.Label>
                <Form.Control type="text" placeholder="Nombre" />
                <Form.Control type="text" placeholder="Apellido Paterno" />
                <Form.Control type="text" placeholder="Apellido Materno" />
              </Form.Group>

              <Form.Group as={Col} controlId="formBasicFechaNacimiento">
                <Form.Label>Fecha de nacimiento</Form.Label>
                <Form.Control as="select" id="Dia" custom>
                  {this.state.days.map((day) => {
                    return (
                      <option key={day} value={day}>
                        {day}
                      </option>
                    );
                  })}
                </Form.Control>
                <Form.Control as="select" id="Mes" custom>
                  {this.state.months.map((month) => {
                    return (
                      <option key={month} value={month}>
                        {month}
                      </option>
                    );
                  })}
                </Form.Control>
                <Form.Control as="select" id="anio" custom>
                  {this.state.years.map((year) => {
                    return (
                      <option key={year} value={year}>
                        {year}
                      </option>
                    );
                  })}
                </Form.Control>
              </Form.Group>
              <Form.Group as={Col} controlId="formBasicEdad">
                <Form.Label>Sexo</Form.Label>
                <Form.Check
                  type="radio"
                  label="Masulino"
                  name="formGenderRadios"
                  id="formGenderRadiosMasculino"
                />
                <Form.Check
                  type="radio"
                  label="Femenino"
                  name="formGenderRadios"
                  id="formGenderRadiosFemenino"
                />
                <Form.Control type="number" placeholder="Edad" />
              </Form.Group>
            </Form.Row>
            <Form.Group controlId="formBasicDomicilio">
              <Form.Control type="text" placeholder="Domicilio" />
            </Form.Group>
            <Form.Row>
              <Form.Group as={Col} controlId="formBasicDatosMadre">
                <Form.Label>Datos Madre</Form.Label>
                <Form.Control type="text" placeholder="Nombre Madre" />
                <Form.Control type="text" placeholder="Ocupacion" />
                <Form.Control type="text" placeholder="Telefono" />
                <Form.Control type="text" placeholder="Grado Escolar" />
              </Form.Group>
              <Form.Group as={Col} controlId="formBasicDatosPadre">
                <Form.Label>Datos Padre</Form.Label>
                <Form.Control type="text" placeholder="Nombre Padre" />
                <Form.Control type="text" placeholder="Ocupacion" />
                <Form.Control type="text" placeholder="Telefono" />
                <Form.Control type="text" placeholder="Grado Escolar" />
              </Form.Group>
            </Form.Row>
            <Form.Row>
              <Form.Group as={Col} controlId="formBasicRecomendado">
                <Form.Label>Recomendado por</Form.Label>
                <Form.Control type="text" placeholder="Nombre" />
                <Form.Control type="text" placeholder="Apellido Paterno" />
                <Form.Control type="text" placeholder="Apellido Materno" />
              </Form.Group>

              {this.state.datosRadioArray.map((radio) => {
                return (
                  <FormGroup as={Col}>
                    <Form.Label>{radio.label}</Form.Label>
                    {radio.checks.map((check) => {
                      return (
                        <Form.Check
                          type="radio"
                          label={check}
                          name={`form${radio.id}Radios`}
                          id={`form${radio.id}Radios${radio.checks.indexOf(
                            check
                          )}`}
                        />
                      );
                    })}
                  </FormGroup>
                );
              })}
            </Form.Row>

            <h1>Antecedentes Medicos Generales</h1>
            {this.state.antecedentesMedicosRadioArray.map((line) => {
              return (
                <Form.Row>
                  {line.map((radio) => {
                    return (
                      <FormGroup as={Col}>
                        <Form.Label>{radio.label}</Form.Label>
                        <br />
                        {radio.checks.map((check) => {
                          return (
                            <Form.Check
                              inline
                              type="radio"
                              label={check}
                              name={`form${radio.id}Radios`}
                              id={`form${radio.id}Radios${radio.checks.indexOf(
                                check
                              )}`}
                            />
                          );
                        })}
                      </FormGroup>
                    );
                  })}
                </Form.Row>
              );
            })}

            <FormGroup>
              <Form.Control
                type="number"
                placeholder="Erupcion Deciduos"
              ></Form.Control>
            </FormGroup>
            <FormGroup>
              <Form.Control
                type="text"
                placeholder="Observaciones"
              ></Form.Control>
            </FormGroup>

            <h1>Analisis Facial</h1>

            {this.state.analisisFacialRadioArray.map((line) => {
              return (
                <Form.Row>
                  {line.map((radio) => {
                    return (
                      <FormGroup as={Col}>
                        <Form.Label>{radio.label}</Form.Label>
                        {radio.checks.map((check) => {
                          return (
                            <Form.Check
                              type="radio"
                              label={check}
                              name={`form${radio.id}Radios`}
                              id={`form${radio.id}Radios${radio.checks.indexOf(
                                check
                              )}`}
                            />
                          );
                        })}
                      </FormGroup>
                    );
                  })}
                </Form.Row>
              );
            })}
            <FormGroup>
              <Form.Control
                type="text"
                placeholder="Observaciones"
              ></Form.Control>
            </FormGroup>

            <h1>Analisis Bucal</h1>

            {this.state.analisisBucalRadioArray.map((line) => {
              return (
                <Form.Row>
                  {line.map((radio) => {
                    return (
                      <FormGroup as={Col}>
                        <Form.Label>{radio.label}</Form.Label>
                        {radio.checks.map((check) => {
                          return (
                            <Form.Check
                              type="radio"
                              label={check}
                              name={`form${radio.id}Radios`}
                              id={`form${radio.id}Radios${radio.checks.indexOf(
                                check
                              )}`}
                            />
                          );
                        })}
                      </FormGroup>
                    );
                  })}
                </Form.Row>
              );
            })}

            <Form.Row>
              <FormGroup as={Col}>
                <Form.Control
                  type="text"
                  placeholder="Dientes presentes"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Supernumerarios"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Descalcificados"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Oclucion Molar Derecha"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Oclusion Canina Derecha"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Sobremordida Vertical"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Mordida Abierta"
                ></Form.Control>
              </FormGroup>
              <FormGroup as={Col}>
                <Form.Control
                  type="text"
                  placeholder="Dientes ausentes"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Malformados"
                ></Form.Control>
                <Form.Control type="text" placeholder="Cariados"></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Oclucion Molar Izquierda"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Oclusion Canina Izquierda"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Sobremordida Horizontal"
                ></Form.Control>
              </FormGroup>
            </Form.Row>

            <FormGroup>
              <Form.Control
                type="text"
                placeholder="Observaciones"
              ></Form.Control>
            </FormGroup>

            <h1>Examen Radiografico</h1>
            <Form.Row>
              <FormGroup as={Col}>
                <Form.Control
                  type="text"
                  placeholder="Dientes Presentes"
                ></Form.Control>
                <Form.Control type="text" placeholder="Cariados"></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Malformados"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Dientes Ausentes"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Supernumerarios"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Incluidos"
                ></Form.Control>
              </FormGroup>
              <FormGroup as={Col}>
                <Form.Control
                  type="text"
                  placeholder="Resorcion Radicular"
                ></Form.Control>
                <Form.Control type="text" placeholder="Quistes"></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Lesiones Periapicales"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Proceso alveolar"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Perdida Cresta Alveolar"
                ></Form.Control>
                <Form.Control
                  type="text"
                  placeholder="Densidad Osea"
                ></Form.Control>
              </FormGroup>
            </Form.Row>
            <Form.Row>
              {this.state.examenRadiograficoRadioArray.map((radio) => {
                return (
                  <FormGroup as={Col}>
                    <Form.Label>{radio.label}</Form.Label>
                    {radio.checks.map((check) => {
                      return (
                        <Form.Check
                          type="radio"
                          label={check}
                          name={`form${radio.id}Radios`}
                          id={`form${radio.id}Radios${radio.checks.indexOf(
                            check
                          )}`}
                        />
                      );
                    })}
                  </FormGroup>
                );
              })}
            </Form.Row>
            <FormGroup>
              <Form.Control
                type="text"
                placeholder="Observaciones"
              ></Form.Control>
            </FormGroup>

            <h1>Analisis de Modelos</h1>
            <Form.Row>
              <FormGroup as={Col}>
                <Form.Control
                  type="number"
                  placeholder="Sobremordida Vertical"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Sobremordida Horizontal"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Oclucion Molar"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Oclucion Canina"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Desviacion Lineal Media"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Longitud Arco Basal Maxilar"
                ></Form.Control>
              </FormGroup>
              <FormGroup as={Col}>
                <Form.Control
                  type="number"
                  placeholder="Longitud Arco Basal Mandibular"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Longitud Arco Dental Maxilar"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Longitud Arco Dental Mandibular"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Discrepancia"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Discrepancia"
                ></Form.Control>
                <Form.Control
                  type="number"
                  placeholder="Diastemas"
                ></Form.Control>
              </FormGroup>
            </Form.Row>

            {this.state.analisisDeModelosRadioArray.map((radio) => {
              return (
                <FormGroup>
                  <Form.Label>{radio.label}</Form.Label>
                  {radio.checks.map((check) => {
                    return (
                      <Form.Check
                        type="radio"
                        label={check}
                        name={`form${radio.id}Radios`}
                        id={`form${radio.id}Radios${radio.checks.indexOf(
                          check
                        )}`}
                      />
                    );
                  })}
                </FormGroup>
              );
            })}

            <Button variant="primary" type="submit">
              Submit
            </Button>
          </Form>
        </div>
      </div>
    );
  }
}
